@use "global" as *;

.heading {
    position: relative;
    padding-top: rclamp(43.33, 56);
    text-align: center;

    &::before {
        position: absolute;
        content: "";
        top: 0;
        left: 50%;
        translate: -50% 0;
        width: rclamp(37.33, 48);
        aspect-ratio: 1;
        background-color: transparent;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
    }
}

.heading__en {
    font-family: $font-family-heading;
    font-size: rclamp(16, 20);
    line-height: 1.25;
}

.heading__ja {
    display: inline-block;
    margin-top: rclamp(6, 8);
    padding-bottom: rclamp(6, 8);
    font-family: $second-font-family;
    font-size: rclamp(20, 32);
    font-weight: $medium;
    line-height: 1.25;
    border-bottom: rclamp(6, 8) solid #FFEE56;
}
