@use "global" as *;

.footer {
    position: relative;
    padding-top: rclamp(40, 120);
}

.footer__inner {
    @include mq("md") {
        position: relative;
        display: flex;
        flex-direction: column;
    }
}

.footer__deco-pawpads {
    position: absolute;
    z-index: $layer-background;

    @include mq("md") {
        top: -40px;
        width: rclamp(70, 100);
        right: 0;
    }

    @include mq("xl") {
        right: auto;
        left: calc(50% + 576px);
    }
}

.sns-links__title {
    display: block;
    font-family: $font-family-en;
    font-weight: $normal;
    font-size: rclamp(20, 24);
    line-height: 1.25;
    text-align: center;

    &::before {
        content: "\\ ";
    }

    &::after {
        content: " /";
    }
}

.sns-links__items {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
    margin-top: 24px;
}

.sns-links__icon {
    position: relative;
    display: grid;
    place-items: center;
    width: rclamp(32, 40);
    aspect-ratio: 1;
    transition: filter 0.3s, -webkit-filter 0.3s;

    &:hover {
        filter: brightness(0.9);
    }

    &::after {
        position: absolute;
        content: "";
        top: 50%;
        left: 50%;
        width: 50px;
        aspect-ratio: 1;
        translate: -50% -50%;
    }
}

.footer__deco-en {
    display: block;
    max-width: rclamp(328, 755);
    margin-top: rclamp(13, 40);
    margin-inline: auto;

    img {
        width: 100%;
    }
}

.footer__address {
    margin-top: -3px;
}

.footer__pagetop {
    margin-top: 23px;
    text-align: center;

    @include mq("md") {
        position: fixed;
        margin-top: 0;
        right: rclamp(30, 40);
        bottom: rclamp(30, 40);
        z-index: $layer-footer;
    }
}

.pagetop {
    width: rclamp(85, 100);
    cursor: pointer;
    transition: filter 0.3s, -webkit-filter 0.3s;

    &:hover {
        filter: brightness(0.9);
    }

    @include mq("md") {
        height: rclamp(83.2, 104);
    }
}

.footer__bottom {
    position: relative;
    padding-top: rclamp(45.7, 120);
    margin-top: rclamp(24, 120);

    &::before {
        position: absolute;
        content: "";
        top: 1px;
        left: 0;
        width: 100%;
        height: rclamp(45.7, 120);
        background-image: url(../images/wave-top-sp.png), linear-gradient(to bottom, transparent 13px, $c-sub 13px);
        background-repeat: repeat-x;
        background-position: left -6px center;
        background-size: contain;

        @include mq("md") {
            background-image: url(../images/wave-top-pc.png), linear-gradient(to bottom, transparent 33px, $c-sub 33px);
        }
    }
}

.footer__copyright {
    display: block;
    padding-bottom: rclamp(25, 32);
    font-size: rclamp(10, 14);
    color: $c-white;
    text-align: center;
    background-color: $c-sub;
}
