@use "global" as *;

.qa {
    margin-top: rclamp(40, 120);
}

.qa__heading {
    &::before {
        background-image: url(../images/qa-icon.png);
    }
}

.qa__boxes {
    margin-top: rclamp(24, 40);
    display: flex;
    flex-direction: column;
    gap: rclamp(11, 24);
}

.qa-box {
    background: $c-white;
    border: 2px solid;
    border-radius: rclamp(12, 16);
    overflow: hidden;
}

.qa-box__head {
    display: flex;
    gap: rclamp(12, 16);
    align-items: center;
    width: 100%;
    padding: 10px 14px;

    @include mq("md") {
        padding-block: 20.5px;
        padding-inline: 22px;
    }

    &.is-open {
        .qa-box__head-toggle {
            &::before {
                transform: translate(-50%, -50%);
            }
        }
    }
}

.qa-box__head-icon,
.qa-box__answer-icon {
    font-family: $font-family-en;
    font-size: rclamp(24, 32);
    line-height: 1.25;
    color: $c-main;
}

.qa-box__head-text {
    flex-grow: 1;
    font-size: rclamp(14, 20);
    font-weight: $bold;
    line-height: 1.59;
    color: $c-font;
    text-align: left;
}

.qa-box__head-toggle {
    position: relative;
    display: block;
    width: rclamp(26, 48);
    aspect-ratio: 1;
    background-color: $c-main;
    border-radius: 50%;

    &::before,
    &::after {
        position: absolute;
        content: "";
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: rclamp(10, 19);
        height: rclamp(2, 3);
        background: $c-white;
        border-radius: 50%;
        transition: transform 0.3s -webkit-transform 0.3s;
    }

    &::before {
        transform: translate(-50%, -50%) rotate(90deg);
    }
}

.qa-box__body {
    display: none;
    padding-top: rclamp(19, 24);
    padding-bottom: rclamp(14, 22);
    padding-inline: rclamp(14, 22);
    background: $c-base;
}

.qa-box__answer {
    display: flex;
    gap: 16px;
    align-items: center;
}

.qa-box__answer-text {
    font-size: rclamp(14, 16);
}
