@use "global" as *;

.drawer-icon {
    position: relative;
    width: 32px;
    height: 27px;

    &::after {
        position: absolute;
        content: "";
        width: calc(100% + 20px);
        height: 64px;
        top: 50%;
        left: 50%;
        translate: -50% -50%;
    }

    &.is-checked {
        .drawer-icon__bar {
            &:nth-of-type(1) {
                top: 10px;
                width: 100%;
                rotate: 45deg;
            }

            &:nth-of-type(2) {
                display: none;
            }

            &:nth-of-type(3) {
                top: 10px;
                rotate: -45deg;
            }
        }
    }
}

.drawer-icon__bar {
    position: absolute;
    left: 0;
    width: 32px;
    height: 3px;
    background-color: $c-font;
    border-radius: 6px;
    transition: rotate 0.3s linear;

    &:nth-of-type(1) {
        inset: 0;
        width: 25.6px;
    }

    &:nth-of-type(2) {
        top: 50%;
        translate: 0 -50%;
        background: $c-font;
    }

    &:nth-of-type(3) {
        bottom: 0;
        background: $c-font;
    }
}

.drawer-icon__dot {
    position: absolute;
    top: 0;
    right: 0;
    left: 28px;
    width: 4px;
    height: 3px;
    background: $c-font;
    border-radius: 6px;
}

.drawer-content {
    display: none;
    position: fixed;
    top: 64px;
    left: 0;
    width: 100%;
    height: calc(100vh - 64px);
    padding-top: 62px;
    padding-bottom: 60px;
    overflow-y: auto;
    background: $c-white;
    z-index: $layer-drawer;
}

.drawer-content__items {
    width: fit-content;
    margin: auto;
}

.drawer-content__link {
    display: block;
    padding-block: 20px;
    font-size: 14px;
    line-height: 1.572;
    text-align: center;
}

.drawer-content__button {
    margin-top: 20px;
    text-align: center;
}
