@use "global" as *;

.fv {
    position: relative;
    margin-top: 64px;
    padding-top: rclamp(17, 34);
}

.fv__bg {
    position: absolute;
    inset: 0;
    width: 100%;
    height: calc(100% - 156px);
    overflow: hidden;
    border-bottom-right-radius: rclamp(24, 40);
    border-bottom-left-radius: rclamp(24, 40);

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center center;
    }

    @include mq("md") {
        height: calc(100% - 96px);
    }
}

.fv__inner {
    position: relative;
    width: 100%;
    max-width: rclamp(335, 848);
    margin-inline: auto;
}

.fv__text {
    font-family: $second-font-family;
    font-size: rclamp(14, 24);
    line-height: 1.25;
    text-align: center;
    text-shadow: 0px 0px 30px $c-white;
}

.fv__title {
    margin-top: rclamp(20, 24);

    @include mq("md") {
        width: 520px;
        max-width: 100%;
        margin-inline: auto;
    }
}

.fv__deco {
    position: relative;
    height: rclamp(522, 558);
    margin-top: 10px;

    @include mq("md") {
        margin-top: 0;
    }
}

.fv__deco-phone {
    position: absolute;
    bottom: rclamp(10, 15);
    left: 50%;
    translate: -50% 0;
    z-index: $layer-up;
}

.deco-phone {
    max-width: 100%;
    width: rclamp(200, 250);
}

.deco-phone__image {
    position: relative;
}

.deco-phone__wrap {
    position: absolute;
    top: calc(50% - 3px);
    left: 50%;
    translate: -50% -50%;
    width: calc(100% - 40px);
    text-align: center;
    background: $c-white;
    border-radius: rclamp(12.8, 16);
    overflow: hidden;

    @include mq("md") {
        top: calc(50% - 6px);
        width: calc(100% - 50px);
    }
}

.deco-phone__head {
    padding: 6px 9.6px;
    font-size: rclamp(12.8, 16);
    font-weight: $bold;
    color: $c-white;
    background: linear-gradient(265deg, #AC3790 -3.52%, #D65A5C 44.94%, #F4C06D 92.38%);

    @include mq("md") {
        padding: 8px 12px;
    }
}

.deco-phone__body {
    padding-top: rclamp(12.8, 14);
    padding-bottom: rclamp(15.2, 20);
}

.deco-phone__lead {
    font-weight: $normal;

    span {
        display: inline-block;
        padding-bottom: 1px;
        border-bottom: 2.4px solid $c-accent;

        @include mq("md") {
            font-size: 14px;
            border-bottom-width: 3px;
        }
    }
}

.deco-phone__text {
    margin-top: rclamp(3.2, 4);
    font-weight: $bold;

    @include mq("lg") {
        font-size: 16px;
    }
}

.deco-phone__button {
    margin-top: rclamp(12.8, 16);
    padding-bottom: 4px;
}

.fv__deco-message {
    position: absolute;
    top: 7px;
    left: 0;
    width: rclamp(142, 244);
    max-width: 100%;

    @include mq("lg") {
        top: 0;
        left: auto;
        right: calc(50% + 143px);
    }
}

.fv__deco-circle {
    position: absolute;
    top: rclamp(0, 17);
    right: 25px;
    width: rclamp(110, 180);
    max-width: 100%;

    @include mq("lg") {
        right: auto;
        left: calc(50% + 117px);
    }
}

.fv__deco-food {
    position: absolute;
    bottom: 0;
    left: 0;
    width: rclamp(90, 212);
    max-width: 100%;
    z-index: $layer-float;

    @include mq("md") {
        bottom: -3px;
    }
}

.fv__deco-cats {
    position: absolute;
    z-index: $layer-float;
    right: 0;
    bottom: 0;

    @include mq("md") {
        bottom: -44px;
    }
}

.deco-cats {
    position: relative;
    width: rclamp(107, 233);
    height: rclamp(127, 294);
}

.deco-cats__1 {
    position: absolute;
    top: 0;
    right: 0;
    width: rclamp(62, 170);

    @include mq("md") {
        left: 0;
    }
}

.deco-cats__2 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 98px;

    @include mq("md") {
        right: 0;
        width: 100%;
    }
}
