@use "global" as *;

.prizes {
    margin-top: rclamp(32, 120);
}

.prizes__inner {
    max-width: rclamp(600, 1084);
    margin-inline: auto;

    @include mq("xl") {
        padding-inline: 30px;
    }
}

.prizes__content {
    position: relative;
    max-width: 100%;
    padding-top: rclamp(20.3, 30);
    padding-bottom: rclamp(30, 49);
    padding-inline: rclamp(20, 56);
    z-index: $layer-up;

    &::before,
    &::after {
        position: absolute;
        content: "";
        z-index: $layer-background;
    }

    &::before {
        top: 0;
        left: 50%;
        translate: -50% 0;
        width: rclamp(238, 372);
        height: rclamp(83, 148);
        background-color: transparent;
        background-image: url(../images/prizes-circle-sp.png);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;

        @include mq("lg") {
            background-image: url(../images/prizes-circle-pc.png);
        }
    }

    &::after {
        bottom: 0;
        left: 0;
        width: 100%;
        height: calc(100% - 83px);
        background-color: $c-white;
        border-radius: 24px;

        @include mq("lg") {
            height: calc(100% - 148px);
        }
    }
}

.prizes__deco-triangle1,
.prizes__deco-triangle2 {
    display: none;
    position: absolute;
    top: 0;
    align-items: center;
    width: calc((100% - 390px) / 2);
    min-height: 148px;

    @include mq("lg") {
        display: flex;
    }
}

.prizes__deco-triangle1 {
    left: 0;
}

.prizes__deco-triangle2 {
    right: 0;
}

.prizes__heading {
    &::before {
        background-image: url(../images/prizes-icon.png);
    }
}

.prizes__text {
    margin-top: rclamp(16, 40);
    text-align: center;
}

.prizes__cards {
    margin-top: rclamp(48, 82);
    display: flex;
    flex-wrap: wrap;
    row-gap: 39px;
    column-gap: 15px;
    justify-content: center;

    @include mq("lg") {
        gap: 0;
    }
}

.prizes__card {
    width: calc(50% - 7.5px);

    @include mq("lg") {
        &:nth-of-type(-n + 2) {
            width: calc(50% - 16px);

            .prizes-card__icon {
                svg {
                    width: rclamp(80, 100);
                    height: rclamp(80, 100);
                }
            }

            .prizes-card__lead {
                min-height: 120px;
                font-size: 20px;
            }

            .prizes-card__number {
                width: 100px;
                top: -50px;
            }

            .prizes-card__button {
                border-radius: 24px;
            }
        }

        &:nth-of-type(2) {
            margin-left: 32px;
        }

        &:nth-of-type(n + 3) {
            width: calc(33.3333% - 24px);
            margin-top: 80px;
        }

        &:nth-of-type(n + 4) {
            margin-left: 36px;
        }
    }
}

.prizes-card {
    position: relative;
    border-radius: 8px;

    &:hover {
        .prizes-card__icon {
            svg {
                .background {
                    fill: #9ED0E0;
                }
            }
        }

        .prizes-card__image {
            img {
                scale: (1.2);
            }
        }
    }
}

.prizes-card__number {
    position: absolute;
    top: -24px;
    left: 50%;
    translate: -50% 0;
    width: rclamp(48, 80);
    z-index: $layer-up;

    @include mq("lg") {
        top: -40px;
    }
}

.prizes-card__inner {
    position: relative;
    background-color: $c-base;
    border-radius: 8px;
    border-radius: rclamp(8, 24);
}

.prizes-card__image {
    display: block;
    overflow: hidden;
    border-top-left-radius: rclamp(8, 24);
    border-top-right-radius: rclamp(8, 24);

    img {
        object-fit: cover;
        transition: scale 0.3s;
    }
}

.prizes-card__lead {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: rclamp(56, 120);
    padding: 5px;
    font-size: rclamp(12, 16);
    text-align: center;

    @include mq("md") {
        padding-top: 7px;
        min-height: rclamp(70, 80);
    }
}

.prizes-card__button {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    border: rclamp(2, 4) solid transparent;
    border-radius: rclamp(8, 16);
    outline: none;

    &:hover {
        border-color: $c-main;
    }
}

.prizes-card__icon {
    svg {
        position: absolute;
        display: block;
        right: -2px;
        bottom: -2px;
        width: rclamp(36, 64);
        height: rclamp(36, 64);
        border-bottom-right-radius: rclamp(8, 16);
    }
}

.prizes-modal {
    width: rclamp(336, 480);
    max-width: calc(100% - 20px);
    height: rclamp(480, 650);
    max-height: calc(100% - 20px);
    padding-top: 0;
    padding-bottom: rclamp(19, 23);
    padding-inline: rclamp(20, 40);
    text-align: center;
    background: $c-white;
    border: none;
    border-radius: 24px;

    &::backdrop {
        background: #000;
        opacity: 0.7;
    }
}

.prizes-modal__image {
    width: calc(100% + 40px);
    margin-left: -20px;

    @include mq("md") {
        width: calc(100% + 80px);
        margin-left: -40px;
    }
}

.prizes-modal__title {
    margin-top: 39px;
    font-size: rclamp(16, 20);
    font-weight: $bold;
}

.prizes-modal__text {
    margin-top: 12px;
}

.prizes-modal__bottom {
    padding-bottom: 4px;
    margin-top: rclamp(16, 24);
}

.prizes__button {
    padding-bottom: 4px;
    margin-top: rclamp(17, 40);
    text-align: center;
}

.prizes__bg {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: rclamp(-43, 0);

    img {
        display: block;
        width: 100%;
        min-width: 580px;
    }

    @include mq("md") {
        display: block;

        img {
            object-fit: cover;
            object-position: center bottom;
        }
    }
}
