@use "global" as *;

.about {
    position: relative;
    margin-top: rclamp(32, 80);
}

.about__deco {
    position: absolute;
}

.about__deco-stripe {
    top: -20px;
    right: calc(50% + 280px);
    width: 250px;
    overflow: hidden;
    border-radius: 24px;

    @include mq("xl") {
        right: calc(50% + 527px);
    }
}

.about__deco-image1 {
    top: 395px;
    right: calc(50% + 128px);
    width: rclamp(200, 480);
    overflow: hidden;
    border-radius: rclamp(24, 40);

    @include mq("md") {
        top: 74px;
        right: calc(50% + 180px);
        z-index: $layer-up;
    }

    @include mq("lg") {
        right: calc(50% + 360px);
    }

    @include mq("xl") {
        right: calc(50% + 614px);
    }
}

.about__deco-slope {
    left: 0;
    bottom: rclamp(61, 149);
    width: 138px;

    @include mq("md") {
        width: 200px;
    }

    @include mq("lg") {
        width: 427px;
        left: auto;
    }

    @include mq("xl") {
        right: calc(50% + 432px);
    }
}

.about__deco-pawpads {
    top: -9px;
    left: calc(50% + 133px);
    width: rclamp(54, 100);

    @include mq("md") {
        top: -20px;
        left: calc(50% + 205px);
    }

    @include mq("lg") {
        top: -60px;
        left: calc(50% + 300px);
    }

    @include mq("xl") {
        top: -80px;
        left: calc(50% + 490px);
    }
}

.about__deco-image2 {
    top: 274px;
    left: calc(50% + 127px);
    width: rclamp(200, 480);
    overflow: hidden;
    border-radius: rclamp(24, 40);

    @include mq("md") {
        left: calc(50% + 180px);
    }

    @include mq("lg") {
        top: 73px;
        left: calc(50% + 360px);
    }

    @include mq("xl") {
        left: calc(50% + 560px);
    }
}

.about__deco-wave {
    bottom: 632px;
    left: calc(50% + 200px);
    width: 874px;

    @include mq("lg") {
        left: calc(50% + 448px);
    }
}

.about__deco-goods1 {
    bottom: 217px;
    left: calc(50% + 118px);
    width: rclamp(48, 101);
    aspect-ratio: 1;

    @include mq("md") {
        left: calc(50% + 280px);
    }

    @include mq("lg") {
        top: 601px;
        left: calc(50% + 300px);
    }

    @include mq("xl") {
        left: calc(50% + 505px);
    }
}

.about__deco-goods2 {
    bottom: 157px;
    left: calc(50% - 3px);
    width: rclamp(133, 308);

    @include mq("md") {
        left: calc(50% + 97px);
    }

    @include mq("lg") {
        bottom: 290px;
        left: calc(50% + 120px);
    }

    @include mq("xl") {
        bottom: 377px;
        left: calc(50% + 374px);
    }
}

.about__inner {
    max-width: 375px;
    padding-inline: rclamp(20, 30);

    @include mq("md") {
        max-width: 500px;
    }

    @include mq("lg") {
        max-width: 640px;
    }

    @include mq("xl") {
        max-width: 908px;
    }
}

.about-top {
    position: relative;
    padding-top: 40px;
    padding-bottom: 36px;
    padding-inline: 20px;
    background-color: $c-white;
    border-radius: rclamp(24, 40);
    z-index: $layer-up;

    &::after {
        position: absolute;
        content: "";
        top: 100%;
        left: 50%;
        translate: -50% 0;
        width: rclamp(100, 140);
        height: rclamp(36, 50);
        background-color: #CCE1E4;
        clip-path: polygon(0 0, 100% 0, 50% 100%);
    }
}

.about-top__picture1 {
    position: absolute;
    top: rclamp(29.87, 40);
    right: calc(50% + 45.5px);
    width: rclamp(91, 150);
    transform-origin: right bottom;

    @include mq("lg") {
        right: calc(50% + 193px);
    }
}

.about-top__picture2 {
    position: absolute;
    top: rclamp(30, 48);
    left: calc(50% + 60.15px);
    width: rclamp(90, 150);
    transform-origin: left bottom;

    @include mq("lg") {
        left: calc(50% + 192px);
    }
}

.about-top__heading {
    &::before {
        background-image: url(../images/about-icon.png);
    }
}

.about-top__lead {
    margin-top: rclamp(24, 34);
    font-size: rclamp(14, 16);
    font-weight: $bold;
    text-align: center;
}

.about-top__text-wrap {
    margin-top: rclamp(24, 32);
    text-align: center;
}

.about-top__text {
    &:not(:first-child) {
        margin-top: 1.5em;

        @include mq("md") {
            margin-top: 1.7em;
        }
    }
}

.about-top__text-strong {
    display: inline-block;
    padding-bottom: rclamp(6, 10);
    margin-top: rclamp(32, 44);
    font-size: rclamp(14, 20);
    font-weight: $bold;
    border-bottom: 2px dashed $c-main;
}

.about-top__button {
    padding-bottom: 4px;
    margin-top: rclamp(24, 32);
    text-align: center;
}

.about__bottom {
    margin-top: rclamp(139.67, 167);
}

.about__deco-en {
    position: relative;
    display: block;
    width: rclamp(354.64, 890.6);
    max-width: 100%;
    margin-inline: auto;
    z-index: $layer-up;
}

.about-swiper {
    position: relative;
    margin-top: -2px;
    z-index: $layer-up;

    @include mq("lg") {
        margin-top: -5px;
    }
}

.about-swiper__slide {
    overflow: hidden;
    border-radius: rclamp(12, 24);
}
