@use "global" as *;

.header {
    position: fixed;
    inset: 0;
    background: $c-white;
    z-index: $layer-header;
    width: 100%;
    height: 64px;
}

.header__inner {
    display: flex;
    align-items: center;
    height: inherit;
    padding-inline: rclamp(20, 24);
}

.header__logo-link {
    img {
        display: block;
        width: 210px;

        @include mq("md") {
            width: 183px;
        }
    }
}

.header__nav {
    display: none;
    height: inherit;
    margin-left: auto;

    @include mq("lg") {
        display: flex;
        gap: 12px;
        align-items: center;
    }
}

.header__link {
    display: flex;
    align-items: center;
    height: 100%;
    font-size: 12px;
    text-decoration: underline;
    text-decoration-color: transparent;
    text-underline-offset: 4px;
    transition: color 0.3s, text-decoration-color 0.2s;

    &:hover {
        color: $c-sub;
        text-decoration-color: $c-sub;
    }
}

.header__button {
    margin-left: 4px;
}

.header__open {
    margin-left: auto;
    line-height: 0;

    @include mq("lg") {
        display: none;
    }
}
