@use "global" as *;

.howto {
    margin-top: rclamp(65, 120);
}

.howto__inner {
    position: relative;
    max-width: 375px;

    @include mq("md") {
        max-width: 1512px;
    }

    @include mq("xl") {
        padding-inline: 30px;
    }
}

.howto__deco {
    position: absolute;
    z-index: $layer-background;
}

.howto__deco-map {
    top: 0;
    right: calc(50% + 39px);
    width: rclamp(149, 460);

    @include mq("md") {
        top: -60px;
        left: auto;
        right: calc(50% + 180px);
    }

    @include mq("xl") {
        right: calc(50% + 260px);
    }
}

.howto__deco-cats {
    top: 0;
    left: calc(50% + 58px);
    z-index: $layer-up;
    width: rclamp(160, 364);
    height: rclamp(85, 191.08);

    @include mq("lg") {
        top: -26px;
        left: calc(50% + 200px);
    }

    @include mq("xl") {
        left: calc(50% + 287px);
    }
}

.howto__deco-pawpads1 {
    top: rclamp(74, 148);
    right: 20px;
    width: rclamp(55, 100);

    @include mq("xl") {
        right: 36px;
    }
}

.howto__deco-pawpads2 {
    top: 516px;
    left: 20px;
    width: rclamp(55, 100);
    rotate: 180deg;

    @include mq("lg") {
        bottom: 40px;
        left: 0;
    }

    @include mq("xl") {
        left: 36px;
    }
}

.howto__deco-pawpads3 {
    right: 20px;
    bottom: 367px;
    width: 55px;
    rotate: 180deg;
}

.howto__heading {
    &::before {
        background-image: url(../images/howto-icon.png);
    }
}

.howto__steps {
    margin-top: rclamp(32, 40);
    display: flex;
    flex-direction: column;
    gap: 50px;

    @include mq("md") {
        flex-direction: row;
        gap: rclamp(7, 32);
        justify-content: center;
    }
}

.howto__step {
    text-align: center;
}

.step {
    &:not(:last-of-type) {
        position: relative;

        &::after {
            position: absolute;
            content: "";
            top: calc(100% + 32px);
            left: 50%;
            translate: 0 -50%;
            width: rclamp(7, 114);
            height: 34px;
            background-color: transparent;
            background-image: url(../images/howto-line-sp.png);
            background-position: center center;
            background-size: contain;
            z-index: $layer-float;

            @include mq("md") {
                position: static;
            }

            @include mq("lg") {
                position: absolute;
                left: auto;
                height: rclamp(16, 24);
                background-image: url(../images/howto-line-pc.png);
                translate: initial;
            }
        }
    }

    @include mq("md") {
        max-width: rclamp(294, 320);

        &:first-of-type {
            &::after {
                top: rclamp(150, 204);
                right: -50px;
            }
        }

        &:nth-of-type(2) {
            margin-top: rclamp(50, 68);

            &::after {
                top: rclamp(100, 136);
                right: -57px;
            }
        }
    }
}

.step__image {
    width: 196px;
    margin: 0 auto;

    @include mq("md") {
        width: 87.5%;
    }
}

.step__title {
    margin-top: rclamp(16, 24);
    font-size: rclamp(16, 20);
    font-weight: $bold;
}

.step__text {
    margin-top: rclamp(16, 17);
    line-height: 1.59;

    @include mq("lg") {
        font-size: rclamp(14, 16);
    }
}

.step__follow-button {
    margin-top: rclamp(14, 16);
}

.howto__button {
    padding-bottom: 4px;
    margin-top: rclamp(16, 48);
    text-align: center;
}
