@use "global" as *;

.address {
    overflow: hidden;
    background-color: $c-white;
    border-radius: 16px;

    @include mq("md") {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
    }
}

.address__map {
    iframe {
        width: 100%;
        height: auto;
        aspect-ratio: 335 / 271;
        vertical-align: top;
    }

    @include mq("md") {
        width: 50%;

        iframe {
            aspect-ratio: 512 / 400;
        }
    }
}

.address__content {
    width: fit-content;
    padding-top: 24px;
    padding-right: 26px;
    padding-bottom: 30px;
    padding-left: 27px;
    margin-inline: auto;

    @include mq("md") {
        padding: 10px;
    }
}

.address__logo {
    width: rclamp(153, 183);
    margin-inline: auto;

    @include mq("md") {
        margin: 0;
    }
}

.address__info {
    display: flex;
    flex-direction: column;
    gap: rclamp(8, 12);
    width: fit-content;
    margin-top: 30px;

    @include mq("md") {
        margin-top: 26px;
    }
}

.address__info-row {
    display: flex;
    align-items: center;
    gap: rclamp(16, 40);
}

.address__info-title {
    position: relative;
    min-width: 64px;
    padding-left: 16px;
    font-style: normal;
    font-weight: $bold;

    &::before {
        position: absolute;
        content: "";
        top: 50%;
        left: 0;
        translate: 0 -50%;
        width: 8px;
        aspect-ratio: 1;
        background-color: $c-main;
        border-radius: 50%;
    }

    @include mq("md") {
        width: 80px;
    }
}

.address__info-text {
    font-style: normal;
}
