@use "global" as *;

.contact {
    padding-block: 40px;
    background: $c-white;

    @include mq("md") {
        padding-top: 120px;
        padding-bottom: 116px;
    }
}

.contact__inner {
    @include mq("md") {
        max-width: rclamp(640, 908);
    }
}

.contact__heading {
    &::before {
        background-image: url(../images/contact-icon.png);
    }
}

.contact__text {
    margin-top: rclamp(24, 32);
    text-align: center;
}

.contact__form {
    margin-top: rclamp(24, 40);
}

.contact__fields {
    display: flex;
    flex-direction: column;
    gap: 24px;
}

.contact__privacy {
    margin-top: rclamp(18, 34);
    text-align: center;
}

.contact__button {
    padding-bottom: 4px;
    margin-top: rclamp(27, 40);
    text-align: center;
}
