@use "global" as *;

.button {
    display: inline-block;
    padding-top: rclamp(4, 8);
    padding-bottom: rclamp(6, 10);
    padding-inline: rclamp(22, 37);
    font-size: rclamp(16, 20);
    font-weight: $bold;
    color: $c-font;
    text-align: center;
    cursor: pointer;
    background: $c-white;
    border: rclamp(2, 3) solid;
    border-radius: 40px;
    box-shadow: 0px 4px 0px 0px $c-font;
    transition: background-color 0.3s;

    &:hover {
        background-color: #FFEE56;
    }
}

.button--small {
    padding-top: rclamp(2, 4);
    padding-bottom: rclamp(4, 6);
    padding-inline: 22px;
    font-size: rclamp(14, 16);
    border-width: 2px;
}

.button--md-small {
    @include mq("md") {
        padding-top: 4px;
        padding-bottom: 6px;
        padding-inline: 22px;
        font-size: 14px;
        border-width: 2px;
    }
}

.button--submit {
    @include mq("md") {
        width: 240px;
        padding-top: 4px;
        padding-bottom: 6px;
        font-size: 16px;
        border-width: 2px;
    }
}

.follow-button {
    position: relative;
    display: inline-block;
    padding-block: 7px;
    padding-right: 7px;
    padding-left: rclamp(39, 44);
    font-size: rclamp(14, 16);
    font-weight: $bold;
    text-align: center;
    background: $c-white;
    border: 1px solid transparent;
    transition: border-color 0.3s;

    &:hover {
        border-color: $c-main;
    }

    &::before {
        position: absolute;
        content: "";
        top: 50%;
        left: 9px;
        translate: 0 -50%;
        width: rclamp(20, 24);
        aspect-ratio: 1;
        background-image: url(../images/icon-instagram.svg);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
    }
}
