@use "global" as *;

.form-field {
    @include mq("md") {
        display: flex;
        gap: 40px;
        align-items: center;

        &:last-of-type {
            align-items: flex-start;
        }
    }
}

.form-field__head {
    display: flex;
    gap: 12px;
    align-items: center;

    @include mq("md") {
        width: 180px;
    }
}

.form-field__label {
    font-size: rclamp(14, 16);
    font-weight: $bold;
    cursor: pointer;
}

.form-field__tag {
    display: inline-block;
    padding: 4px 8px;
    font-size: 12px;
    font-weight: $bold;
    color: $c-white;
    background-color: $c-accent;
    border-radius: 4px;

    @include mq("md") {
        margin-bottom: 1px;
    }
}

.form-field__body {
    margin-top: 8px;

    @include mq("md") {
        width: calc(100% - 180px - 40px);
        margin-top: 0;
    }
}

.form-text,
.form-textarea {
    width: 100%;
    height: 56px;
    padding: 14.5px 15px;
    font-size: rclamp(14, 16);
    background: $c-base;
    border: 1px solid transparent;
    border-radius: 8px;

    &:focus {
        background-color: #E9F6F8;
        border-color: $c-main;
    }

    &.is-error {
        background-color: #FFF0F7;
        border-color: $c-accent;
    }

    &::placeholder {
        color: #CCCCCC;
    }
}

.form-select {
    position: relative;
    width: 100%;
    height: 56px;
    padding: 14.5px 15px;
    font-size: rclamp(14, 16);
    appearance: none;
    cursor: pointer;
    background-image: url(../images/arrow-bottom.png), linear-gradient(to left, #9ed0e0 52px, #f5f5f5 52px);
    background-repeat: no-repeat no-repeat;
    background-position: center right 10px, center center;
    background-size: 33px, cover;
    border: 1px solid transparent;
    border-radius: 8px;

    &.is-error {
        background-image: url(../images/arrow-bottom.png), linear-gradient(to left, $c-accent 52px, #FFF0F7 52px);
        border-color: $c-accent;
    }
}

.form-field__radios {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    align-items: center;
    padding: 0;

    @include mq("md") {
        height: 56px;
        padding-left: 2px;
    }
}

.form-radio__input {
    position: absolute;
    width: 1px;
    aspect-ratio: 1;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);

    &:checked + .form-radio__text {
        font-weight: $bold;
    }

    &:checked + .form-radio__text::after {
        opacity: 1;
    }
}

.form-radio__text {
    position: relative;
    padding-left: 32px;
    font-size: rclamp(14, 16);

    &::before,
    &::after {
        position: absolute;
        content: "";
        top: 50%;
        translate: 0 -50%;
        border-radius: 50%;
        aspect-ratio: 1;
    }

    &::before {
        left: 0;
        width: 24px;
        background: $c-base;
        border: 1px solid transparent;
    }

    &::after {
        left: 6px;
        width: 12px;
        background: $c-main;
        opacity: 0;
    }
}

.form-textarea {
    height: 160px;
}

.form-checkbox {
    cursor: pointer;
}

.form-checkbox__input {
    position: absolute;
    width: 1px;
    aspect-ratio: 1;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);

    &:checked + .form-checkbox__text::after {
        opacity: 1;
    }
}

.form-checkbox__text {
    position: relative;
    padding-left: 36px;
    font-size: rclamp(14, 16);

    &::before,
    &::after {
        position: absolute;
        content: "";
        top: 50%;
        left: 0;
        translate: 0 -50%;
        width: 24px;
        aspect-ratio: 1;
    }

    &::before {
        background: $c-base;
        border: 1px solid transparent;
        border-radius: 4px;
    }

    &::after {
        background-image: url(../images/icon-checked.png);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        opacity: 0;
    }
}
