@use "sass:math";

@function rclamp($min, $max, $min-view-port: 768, $max-view-port: 1000) {
  $valiable-part: calc(($max - $min) / ($max-view-port - $min-view-port)); // vw単位でのスケールを計算
  $constant: calc(($max - $max-view-port * $valiable-part) / 16);
  $min-rem: calc($min / 16 * 1rem);
  $max-rem: calc($max / 16 * 1rem);
  $variable-rem: calc($constant * 1rem);
  $variable-vw: calc(100 * $valiable-part * 1vw);

  @return clamp($min-rem, $variable-rem + $variable-vw, $max-rem);
}

// 使用例
.example {
    font-size: rclamp(13, 16);
}

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$c-font: #4A3636;
$c-white: #FFFFFF;
$c-main: #9ED0E0;
$c-sub: #67B0C7;
$c-base: #F5F5F5;
$c-accent: #CE2073;

////////////////////
// フォント設定
////////////////////

$base-font-family: "Noto Sans JP", serif;
$second-font-family: "Kiwi Maru", serif;
$font-family-en: "Josefin Sans", sans-serif;
$font-family-heading: "Courgette", cursive;
// ※検証ツールでしっかり反映されているか確認する

// フォントウェイト
$regular: 300;
$normal: 400;
$medium: 500;
$bold: 700;

body {
  font-family: $base-font-family;
  font-weight: $normal;
  color: $c-font;
  font-size: rclamp(12, 16);
  line-height: 1.6;
  background: #E9F6F8;
  overflow: auto;

  &.is-fixed {
    overflow: hidden;
  }
}


////////////////////
// メディアクエリとブレークポイントの設定
////////////////////

//ブレイクポイント
$breakpoints: (
  sm: 600,
  md: 768,
  lg: 1024,
  xl: 1440,
);

//メディアクエリー
$mediaquerys: (
  sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
);

// @include mq(){}で書くとブレークポイントが反映される（初期値はmd）
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}


////////////////////
// 階層の指定
////////////////////

$layer-modal: 100;
$layer-drawer: 30;
$layer-header: 20;
$layer-footer: 10;
$layer-float: 3;
$layer-up: 2;
$layer-default: 1;
$layer-background: -1;


////////////////////
// 非表示設定
////////////////////

//768px以上を表示
.hidden-sp {
  display: none;
  @include mq("md") {
    display: block;
  }
}

//モバイルのみ表示
.hidden-pc {
  @include mq("md") {
    display: none;
  }
}
