@use "global" as *;

.spots {
    position: relative;
    padding-block: 45.7px;
    margin-top: -45.7px;

    @include mq("lg") {
        padding-block: 120px;
        margin-top: -120px;
    }

    &::before,
    &::after {
        position: absolute;
        content: "";
        left: 0;
        width: 100%;
        height: rclamp(45.7, 120);
        background-color: transparent;
        background-repeat: repeat-x;
        background-size: contain;
    }

    &::before {
        top: 1px;
        background-image: url(../images/wave-top-sp.png), linear-gradient(to bottom, transparent 13px, $c-sub 13px);
        background-position: left -3px center;

        @include mq("md") {
            background-image: url(../images/wave-top-pc.png), linear-gradient(to bottom, transparent 33px, $c-sub 33px);
        }
    }

    &::after {
        bottom: 1px;
        background-image: url(../images/wave-bottom-sp.png), linear-gradient(to top, transparent 13px, $c-sub 13px);
        background-position: left -6px center;

        @include mq("md") {
            background-image: url(../images/wave-bottom-pc.png), linear-gradient(to top, transparent 33px, $c-sub 33px);
        }
    }
}

.spots__deco-pawpads {
    position: absolute;
    bottom: 120px;
    left: 0;
    width: rclamp(80, 100);
}

.spots__content {
    padding-bottom: 14.86px;
    background-color: $c-sub;

    @include mq("md") {
        padding-bottom: 0;
    }
}

.spots__top {
    @include mq("lg") {
        display: flex;
        column-gap: 32px;
        padding-left: max(30px, 50% - 512px);
    }
}

.spots__head {
    text-align: center;

    @include mq("lg") {
        width: 56px;
    }
}

.spots__heading {
    position: relative;
    display: inline-block;
    padding-left: 36px;
    font-family: $second-font-family;
    font-size: rclamp(28, 40);
    font-weight: $medium;
    line-height: 1.25;
    color: $c-white;

    &::before {
        position: absolute;
        content: "";
        top: 50%;
        left: 0;
        translate: 0 -50%;
        width: rclamp(28, 56);
        aspect-ratio: 1;
        background-color: transparent;
        background-image: url(../images/icon-star.png);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
    }

    @include mq("lg") {
        padding-top: 72px;
        padding-block: 3px;
        letter-spacing: 0.25em;
        writing-mode: vertical-lr;

        &::before {
            top: 0;
            left: 50%;
            translate: -50% 0;
        }
    }
}

.spots-swiper {
    margin-top: 25px;

    @include mq("lg") {
        width: calc(100% - 56px - 32px);
        margin-top: 0;
    }
}

.spots-swiper__slide {
    height: auto;
}

.spots-card {
    height: 100%;
    padding-inline: rclamp(24, 32);
    padding-bottom: rclamp(30, 40);
    overflow: hidden;
    background: $c-white;
    border-radius: rclamp(12, 24);
}

.spots-card__image {
    display: block;
    width: calc(100% + 48px);
    margin-left: -24px;

    @include mq("lg") {
        width: calc(100% + 64px);
        margin-left: -32px;
    }
}

.spots-card__title {
    margin-top: rclamp(16, 25);
    font-size: rclamp(16, 20);
    text-align: center;
}

.spots-card__text {
    margin-top: rclamp(16, 25);
}

.spots__inner {
    @include mq("md") {
        max-width: 1084px;
        padding-inline: 30px;
        margin-inline: auto;
    }
}

.spots__arrows {
    position: relative;
    max-width: 700px;
    margin-top: -10px;
    margin-inline: auto;
    z-index: $layer-up;

    @include mq("xl") {
        margin-top: -32px;
        max-width: 944px;
    }
}

.spots-swiper__arrows {
    display: flex;
    justify-content: space-between;
}

.spots-swiper__prev,
.spots-swiper__next {
    position: static;
    width: 80px;
    height: 80px;
    background-color: #FFEE56;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 60px;
    border-radius: 50%;
    transition: background-position 0.3s;

    &::after {
        content: "";
    }
}

.spots-swiper__prev {
    background-image: url(../images/arrow-left.png);

    &:hover {
        background-position: center left 40%;
    }
}

.spots-swiper__next {
    background-image: url(../images/arrow-right.png);

    &:hover {
        background-position: center right 40%;
    }
}

.spots__bottom {
    margin-inline: auto;

    @include mq("xl") {
        display: flex;
        gap: 30px;
        align-items: center;
        justify-content: space-between;
        margin-top: 40px;
    }
}

.spots__deco-en {
    display: block;
    width: rclamp(254, 494);
    margin-inline: auto;
    line-height: 1;

    @include mq("xl") {
        margin: 0;
    }
}

.spots__text-wrap {
    margin-top: 15px;

    @include mq("xl") {
        margin: 0;
    }
}

.spots__text {
    font-size: rclamp(11.444, 16);
    color: $c-white;
    text-align: center;

    @include mq("xl") {
        text-align: right;
    }
}

.spots__button {
    padding-bottom: 4px;
    margin-top: 24px;
    text-align: center;

    @include mq("xl") {
        text-align: right;
    }
}
