@use "global" as *;

.requirements {
    margin-top: rclamp(30, 120);
}

.requirements__content {
    padding-top: rclamp(16.5, 32);
    padding-right: rclamp(15.5, 48);
    padding-bottom: rclamp(32, 44);
    padding-left: rclamp(16, 48);
    background: rgba(255, 255, 255, 0.5);
    border: 8px solid $c-white;
}

.requirements__heading {
    &::before {
        background-image: url(../images/requirements-icon.png);
    }
}

.requirements__table {
    margin-top: rclamp(24, 40);
}

.requirements-table {
    width: 100%;
    table-layout: fixed;
    border-collapse: collapse;
}

.requirements-table__row {
    display: flex;
    flex-direction: column;
    gap: rclamp(6, 20);
    padding-bottom: 14px;
    text-align: left;
    border-bottom: 1px solid #CCCCCC;

    @include mq("lg") {
        flex-direction: row;
        padding-bottom: 9px;
        padding-left: 16px;
    }

    &:not(:first-child) {
        padding-top: 12px;
    }

    &:last-child {
        padding-bottom: 0;
        border: none;
    }
}

.requirements-table__head,
.requirements-table__body {
    text-align: left;
    vertical-align: middle;
}

.requirements-table__head {
    position: relative;
    height: 100%;
    padding-left: 16px;
    font-size: rclamp(14, 16);
    font-weight: $bold;

    @include mq("md") {
        flex-grow: 1;
        width: 188px;
    }

    &::before {
        position: absolute;
        content: "";
        top: 50%;
        left: 0;
        translate: 0 -50%;
        width: 8px;
        aspect-ratio: 1;
        background-color: $c-main;
        border-radius: 50%;
    }
}

.requirements-table__body {
    @include mq("lg") {
        width: calc(100% - 224px);
    }
}

.requirements-table__text {
    position: relative;
    padding-left: 1.5em;
    line-height: rclamp(19, 26);

    &::before {
        position: absolute;
        content: "・";
        top: 0.1em;
        left: 0.2em;
    }
}

.requirements__bg {
    display: block;
    margin-top: rclamp(32, 120);
}
